<template>
    <v-container>
        <v-form
            @submit.prevent="submitLogin"
        >
            <v-text-field
                label="名前"
                v-model="form.username"
            ></v-text-field>
            <v-text-field
                label="パスワード"
                type="password"
                v-model="form.password"
            ></v-text-field>
            <v-btn
                type="submit"
            >ログイン</v-btn>
        </v-form>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            form: {
                username: '',
                password: ''
            }
        }
    },

    methods: {
        submitLogin() {
            this.$store.dispatch('login', {
                username: this.form.username,
                password: this.form.password
            })
            this.$router.replace('/')
        }
    }
}
</script>