<template>
    <div>
        <router-link
            :to="{name: 'detail', params: {id: article.id}}"
            class="article_item"
        >
            <v-card
                class="mx-3 mb-5"
            >
                <v-img
                    height="250"
                    src="https://cdn.vuetifyjs.com/images/cards/cooking.png"
                ></v-img>
                <v-card-title>{{ article.title }}</v-card-title>
                <v-card-text
                    class="d-flex justify-end"
                >
                    <small><v-icon small>mdi-update</v-icon>{{ getUpdatedAt(article.updated_at) }}</small>
                </v-card-text>
            </v-card>
        </router-link>
    </div>
</template>

<script>
export default {
    props: ["article"],

    methods: {
        getUpdatedAt(date) {
            const updated_at =  new Date(date)
            return updated_at.getFullYear() + '/' + (updated_at.getMonth() + 1) + '/' + updated_at.getDate()
        }
    }
}
</script>

<style scoped>
    .article_item {
        color: inherit;
        text-decoration: none;
    }
</style>