<template>
    <v-container>
        <v-row>
            <v-col
                xl=9
                lg=9
                md=9
                sm=12
                cols=12
                class="d-flex flex-wrap"
            >
                <Article
                    v-for="(blog, key) in blogs"
                    :key="key"
                    :article=blog
                    class="article"
                ></Article>
            </v-col>
            <v-col
                xl=3
                lg=3
                md=3
                sm=12
                cols=12
            >
                <Introduction/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import api from '../services/api'
import Article from '../components/Article'
import Introduction from '../components/Introduction'

export default {
    components: {
        Article,
        Introduction,
    },

    data() {
        return {
            blogs: []
        }
    },

    created() {
        api.get('blog/')
        .then(response => {
            this.blogs = response.data
        })
    }
}
</script>

<style scoped lang="scss">
    .article {
        width: 50%;

        @media screen and (max-width: 600px) {
            width: 100%;
        }

    }
</style>
