<template>
    <v-container>
        <v-row class="my-5">
            <v-col
                xl=9
                lg=9
                md=9
                sm=12
                cols=12
            >
                <v-card
                    id="content"
                >
                    <span><v-icon>mdi-update</v-icon>{{ updated_at.getFullYear() + '/' + (updated_at.getMonth()+1) +'/' + updated_at.getDate() }}</span>
                    <div class="d-flex justify-end">
                        <v-btn
                            v-if="isLoggedIn"
                            class="red mx-2"
                            @click.prevent="deleteArticle"
                        >
                            削除
                        </v-btn>
                        <router-link
                            v-if="isLoggedIn"
                            :to="{name: 'update', params: {id: article.id}}"
                            tag="div"
                        >
                            <v-btn
                                class="blue"
                            >編集</v-btn>
                        </router-link>
                    </div>
                    <v-card-title id="content_title" class="text-md-h3 text-xs-h4">{{ article.title }}</v-card-title>
                    <markdown-it-vue class="md-body" :content="article.content"></markdown-it-vue>

                </v-card>
            </v-col>
            <v-col
                xl=3
                lg=3
                md=3
                sm=12
                cols=12
            >
                <introduction/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import api from '../services/api'
import Introduction from '../components/Introduction'

import MarkdownItVue from 'markdown-it-vue'

export default {
    components: {
        Introduction,
        MarkdownItVue
    },

    data() {
        return {
            isLoggedIn: false,
            article: {},
            updated_at: null,
        }
    },

    methods: {
        getArticle() {
            api.get('/blog/' + this.$route.params.id)
            .then(response => {
                this.article = response.data
                console.log(this.article)
                this.updated_at = new Date(this.article.updated_at)
            })
        },

        deleteArticle() {
            if (window.confirm('本当に削除しますか？')) {
                api.delete('/blog/' + this.$route.params.id)
                .then(() => {
                    this.$router.replace('/')
                })
            }
        }
    },

    created () {
        this.isLoggedIn = this.$store.getters.isLoggedIn
        this.getArticle()
    }
}
</script>

